import React, { useState, useEffect, useRef } from 'react';
import { Container, Typography, Box, Button } from '@mui/material';
import ImageGallery from './ImageGallery';
import { ipcRenderer } from 'electron';
import path from 'path';

// 画像データのセットを用意します（実際のアプリケーションでは、これらは動的に読み込まれる可能性があります）
const imageSets = [
    [
      { id: 1, src: 'path/to/image1.jpg', alt: 'Image 1' },
      { id: 2, src: 'path/to/image2.jpg', alt: 'Image 2' },
      { id: 3, src: 'path/to/image3.jpg', alt: 'Image 3' },
      { id: 4, src: 'path/to/image4.jpg', alt: 'Image 4' },
    ],
    // 他の画像セットを追加...
  ];

  function App() {
    console.log('App component is rendering');
  
    const [currentSetIndex, setCurrentSetIndex] = useState(0);
    const [images, setImages] = useState([]);
    const [selectedImage, setSelectedImage] = useState(null);
    const [selectedRegions, setSelectedRegions] = useState({});
    const [saveDirectory, setSaveDirectory] = useState('');
    const [sessionFolder, setSessionFolder] = useState('');
    const imageGalleryRef = useRef();
  
    const { gazeData, isConnected, error, reconnect } = useEyeTracker('ws://localhost:8765');
  
    useEffect(() => {
      console.log('App component mounted');
      window.ipcRenderer.invoke('get-initial-save-directory').then((initialSaveDir) => {
        console.log('Initial save directory:', initialSaveDir);
        setSaveDirectory(initialSaveDir);
        createSessionFolder(initialSaveDir);
      }).catch(error => {
        console.error('Error getting initial save directory:', error);
      });
    }, []);
  
    useEffect(() => {
      console.log('Eye tracker connection status:', isConnected);
      console.log('Eye tracker error:', error);
      console.log('Gaze data:', gazeData);
    }, [isConnected, error, gazeData]);
    
    const createSessionFolder = async (directory) => {
      const newSessionFolder = await ipcRenderer.invoke('create-session-folder', directory);
      setSessionFolder(newSessionFolder);
    };
  
    const handleImageSelect = (image) => {
      setSelectedImage(image);
    };
  
    const handleRegionSelect = (image, regions) => {
      setSelectedRegions(prev => ({
        ...prev,
        [image.id]: regions
      }));
    };
  
    const handleSelectDirectory = async () => {
      const result = await ipcRenderer.invoke('select-directory');
      if (result) {
        setSaveDirectory(result);
        createSessionFolder(result);
      }
    };
  
    const handleNextStep = async () => {
      const dataToSave = {
        selectedImage: selectedImage,
        selectedRegions: selectedRegions,
        gazeData: gazeData // 視線データを保存データに追加
      };
  
      try {
        const fileName = new Date().toISOString().replace(/[-:T]/g, '').split('.')[0] + '.json';
        await ipcRenderer.invoke('save-data', sessionFolder, fileName, dataToSave);
        console.log('Data saved successfully');
  
        const nextIndex = (currentSetIndex + 1) % imageSets.length;
        setCurrentSetIndex(nextIndex);
        setImages(imageSets[nextIndex]);
        
        setSelectedImage(null);
        setSelectedRegions({});
  
        if (imageGalleryRef.current) {
          imageGalleryRef.current.resetSelection();
        }
  
      } catch (error) {
        console.error('Failed to save data:', error);
        alert('Failed to save data. Please try again.');
      }
    };
  
    return (
      <Box sx={{ width: '100%', overflowX: 'auto' }}>
        <Container maxWidth={false} style={{ padding: '1rem', minWidth: '1250px' }}>
          <Typography variant="h4" component="h1" gutterBottom align="center">
            Image Selection System
          </Typography>
          <Box mb={2}>
            <Button variant="contained" color="primary" onClick={handleSelectDirectory}>
              Select Save Directory
            </Button>
            {saveDirectory && (
              <Typography variant="body2" mt={1}>
                Save Directory: {saveDirectory}
              </Typography>
            )}
          </Box>
          <Box mb={2}>
            <Typography variant="body1">
              Eye Tracker Status: {isConnected ? 'Connected' : 'Disconnected'}
            </Typography>
            {!isConnected && (
              <Button variant="contained" color="secondary" onClick={reconnect}>
                Reconnect to Eye Tracker
              </Button>
            )}
            {error && (
              <Typography variant="body2" color="error">
                Eye Tracker Error: {error}
              </Typography>
            )}
            {gazeData && (
              <Typography variant="body2">
                Gaze Position: X: {gazeData.x.toFixed(4)}, Y: {gazeData.y.toFixed(4)}
              </Typography>
            )}
          </Box>
          <ImageGallery 
            ref={imageGalleryRef}
            images={images} 
            onImageSelect={handleImageSelect} 
            onRegionSelect={handleRegionSelect}
            selectedImage={selectedImage}
            gazeData={gazeData}
          />
          <Box mt={4} display="flex" justifyContent="center">
            <Button
              variant="contained"
              color="primary"
              onClick={handleNextStep}
              disabled={!selectedImage || !isConnected} // 画像が選択されていないか、Eye Trackerが接続されていない場合はボタンを無効化
            >
              Next Step
            </Button>
          </Box>
        </Container>
      </Box>
    );
  }
  
  export default App;
